.navigationMenu {
    display: flex;
    flex-direction: row;
    gap: 16px;
    align-items: flex-end;
}

.navItem {
    text-decoration: none;
    color:#6221d3;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-radius: 6px;
    padding: 0 5px;
    border-bottom: 2px solid transparent;
}

.navItem:hover{
    border-bottom: 2px solid #00ffff;
    color: white;
}

.logo {
    height: 160px;
}