.cardWheelContainer {
    background-image: linear-gradient(to right, transparent, rgba(98, 33, 211, 0.2), transparent);
    color: white;
    text-align: center;
    padding: 2vh;
}

.cardWheel {
    display: flex;
    flex-direction: row;
    padding: 2vh 3vw;
    justify-content: space-between;
    align-items: center;
    gap: 3vw;
    overflow-x: scroll;
}
