* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scrollbar-width: thin;
  scrollbar-color: transparent rgba(0, 0, 239, 0.526);
}

*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(95, 156, 247, 0.108);
  border-radius: 20px;
}


.body {
  background-color: #0c1633;
  background-image: linear-gradient(rgb(18, 0, 78) 15%, #0c1633 85%);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

header {
  background-image: linear-gradient(to bottom, rgb(18, 0, 78) 90%, rgba(0, 0, 239, 0.526));
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 26vh;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
}

main {
  background-image: linear-gradient(to right, transparent, rgba(95, 237, 247, 0.1), transparent);
  min-height: 70vh;
  width: 100%;
  border-radius: 10%;
  padding: 5vh 2vw;
}

footer{
  color: rgba(255, 255, 255, 0.377);
  background-image: linear-gradient(to top, transparent 70%, rgba(0, 0, 239, 0.526));
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 10vh;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}